<template>
  <div class="flex">
    <input class="m-1 p-1 bg-black text-white" v-model="inputMessage" />
    <button class="m-1 p-1 bg-black text-white" @click="onButton()">
      CLICK
    </button>
  </div>
  <div class="w-96 h-96">
    <iframe
      ref="myiframe"
      src="/testIframe/exIframe.html"
      width="100%"
      height="100%"
      scrolling="no"
      frameborder="0"
    >
    </iframe>
  </div>
</template>

<script>
/* 
https://www.programmersought.com/article/94511230199/ 
vue.js <-> iframe 
*/

export default {
  data() {
    return {
      inputMessage: "",
      iframeWin: null,
    };
  },
  mounted() {
    window.addEventListener("message", (event) => {
      if (event.data.cmd === undefined) return;
      console.log("mounted recv message", event.data);

      if (event.data.cmd == "vue_recv") {
        this.inputMessage = event.data.value;
      }
    });
    this.iframeWin = this.$refs.myiframe.contentWindow;
  },
  methods: {
    onButton() {
      console.log("onButton send", this.inputMessage);

      const req = {
        cmd: "vue_send",
        value: this.inputMessage,
      };
      this.iframeWin.postMessage(req, "*");
      this.inputMessage = "";
    },
  },
};
</script>
